/* Create extendable styles for a flex-styled list, horizontal by default. */
.contact-me, .app-main-nav ul, .things-I-love, .favorite-colors, .print-resume {
  display: flex;
  margin: 1rem -0.5rem;
  padding: 0; }
  .contact-me li, .app-main-nav ul li, .things-I-love li, .favorite-colors li, .print-resume li {
    display: unset;
    flex: 1 1 auto;
    margin: 0.5rem;
    padding: 0 1rem;
    text-align: center; }
    .contact-me li.print-only, .app-main-nav ul li.print-only, .things-I-love li.print-only, .favorite-colors li.print-only, .print-resume li.print-only {
      display: none; }

