/* More general layout styling */
main > section {
  padding: 5%; }

/* Styles for the app header */
.app-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; }
  .app-header h1 {
    margin-bottom: 0;
    margin-top: auto; }
    .app-header h1 + p {
      margin-top: 0.5rem; }
  .app-header a {
    color: white; }

.contact-me {
  font-size: 1.25rem;
  margin-bottom: auto; }
  .contact-me li {
    align-items: center;
    display: flex; }
    .contact-me li:after {
      content: "\25C8";
      font-size: 50%;
      margin: 0 -1.5rem 0 1.5rem; }
    .contact-me li:last-child:after {
      display: none; }

.app-main-nav ul li {
  align-items: center;
  display: flex; }
  .app-main-nav ul li:after {
    content: "|";
    font-size: 50%;
    margin: 0 -1.5rem 0 1.5rem; }
  .app-main-nav ul li:last-child:after {
    display: none; }

/* Styles specific to the about section */
.things-I-love {
  flex-wrap: wrap; }
  .things-I-love li {
    background: #efefef;
    padding: 2rem 1.5rem; }

.favorite-colors {
  flex-wrap: wrap; }
  .favorite-colors li {
    padding: 1.5rem 2rem; }
    .favorite-colors li:hover h4,
    .favorite-colors li:hover p {
      opacity: 1; }
  .favorite-colors h4 {
    text-transform: capitalize; }
  .favorite-colors h4,
  .favorite-colors p {
    margin: 0.8rem 0;
    opacity: 0;
    -webkit-transition: opacity 300ms ease;
    transition: opacity 300ms ease; }

/* General/simple classes */
.large {
  font-size: 165%;
  line-height: 0;
  vertical-align: sub; }

.print-only {
  display: none; }

.print-resume {
  flex-direction: column; }
  .print-resume button {
    background-color: #fb7d00;
    border: 0 none;
    border-radius: 2.25rem;
    box-shadow: inset 0 0 15px 0 rgba(200, 100, 0, 0.5);
    color: white;
    cursor: pointer;
    font-size: 1rem;
    font-weight: 700;
    padding: 1.25rem 1.75rem;
    text-transform: uppercase;
    -webkit-transition: all 300ms ease;
    transition: all 300ms ease; }
    .print-resume button:hover {
      background-color: #ff1e65;
      box-shadow: inset 0 0 15px 0 rgba(234, 0, 74, 0.5); }

