/* Print styles */
/* Print styles for resume */
@media print {
  #root .print-only {
    display: inherit; }
  #root .app-main-nav,
  #root .about h2,
  #root .about h3,
  #root .background,
  #root .passions,
  #root .fun-fact,
  #root .work,
  #root .print-resume {
    display: none; }
  #root .app-header {
    min-height: 0; } }

